.Loader_lds-ellipsis__268ir {
    display: inline-block;
    position: relative;
    width: 80px;
    height: 80px;
    text-align: center;
}
.Loader_lds-ellipsis__268ir div {
    position: absolute;
    top: 33px;
    width: 13px;
    height: 13px;
    border-radius: 50%;
    background: #fff;
    animation-timing-function: cubic-bezier(0, 1, 1, 0);
}
.Loader_lds-ellipsis__268ir div:nth-child(1) {
    left: 8px;
    animation: Loader_lds-ellipsis1__osTdy 0.6s infinite;
}
.Loader_lds-ellipsis__268ir div:nth-child(2) {
    left: 8px;
    animation: Loader_lds-ellipsis2__MeKh2 0.6s infinite;
}
.Loader_lds-ellipsis__268ir div:nth-child(3) {
    left: 32px;
    animation: Loader_lds-ellipsis2__MeKh2 0.6s infinite;
}
.Loader_lds-ellipsis__268ir div:nth-child(4) {
    left: 56px;
    animation: Loader_lds-ellipsis3__22tHn 0.6s infinite;
}
@keyframes Loader_lds-ellipsis1__osTdy {
    0% {
        transform: scale(0);
    }
    100% {
        transform: scale(1);
    }
}
@keyframes Loader_lds-ellipsis3__22tHn {
    0% {
        transform: scale(1);
    }
    100% {
        transform: scale(0);
    }
}
@keyframes Loader_lds-ellipsis2__MeKh2 {
    0% {
        transform: translate(0, 0);
    }
    100% {
        transform: translate(24px, 0);
    }
}

